import { useEffect, useMemo, useState } from "react";
import Highcharts from "highcharts";
import HighchartsReact from "highcharts-react-official";
import moment from "moment";

function OnePage({ isPage }) {
  const [data, setData] = useState([]);
  const [timeLine, setTimeLine] = useState("1m");

  const correctData = useMemo(() => {
    let result = [...data];
    const arr = [];
    result.forEach((item) => arr.push(item[1]));
    return arr;
  }, [data]);

  const options = {
    chart: {
      zoomType: "x",
      backgroundColor: "#1c1b1b",
    },
    title: {
      text: "",
    },
    xAxis: {
      type: "datetime",
    },
    yAxis: {
      title: {
        text: "",
      },
    },
    legend: {
      enabled: true,
    },
    plotOptions: {
      area: {
        fillColor: {
          linearGradient: {
            x1: 0,
            y1: 0,
            x2: 0,
            y2: 1,
          },
          stops: [
            [0, Highcharts.getOptions().colors[0]],
            [
              1,
              Highcharts.color(Highcharts.getOptions().colors[0])
                .setOpacity(0)
                .get("rgba"),
            ],
          ],
        },
        lineWidth: 0.5,
        states: {
          hover: {
            lineWidth: 0.5,
          },
        },
        threshold: null,
      },
    },
    series: [
      {
        type: "area",
        name: "USD",
        data: correctData,
      },
    ],
    tooltip: {
      backgroundColor: Highcharts.getOptions().colors[0],
      borderColor: 'black',
      borderRadius: 10,
      borderWidth: 3
  }
  };

  useEffect(() => {
    (async () => {
      const response = await fetch(
        `https://api.coinstats.app/public/v1/charts?period=${timeLine}&coinId=${isPage.id}`
      );
      const { chart } = await response.json();
      setData(chart);
    })();
  }, [timeLine]);

  return (
    <div className="project__chart">
      <HighchartsReact highcharts={Highcharts} options={options} />
      <div className="project__chart-date">
        <span className={timeLine === '24h' ? 'red' : ''} onClick={() => setTimeLine('24h')} >24H</span>
        <span className={timeLine === '1w' ? 'red' : ''} onClick={() => setTimeLine('1w')} >1W</span>
        <span className={timeLine === '1m' ? 'red' : ''} onClick={() => setTimeLine('1m')} >1M</span>
        <span className={timeLine === '3m' ? 'red' : ''} onClick={() => setTimeLine('3m')} >3M</span>
        <span className={timeLine === '6m' ? 'red' : ''} onClick={() => setTimeLine('6m')} >6M</span>
        <span className={timeLine === '1y' ? 'red' : ''} onClick={() => setTimeLine('1y')} >1Y</span>
        <span className={timeLine === 'all' ? 'red' : ''} onClick={() => setTimeLine('all')} >ALL</span>
      </div>
    </div>
  );
}

export default OnePage;
